<template>
    <div class="bg-white d-flex justify-content-around border-top fixed-bottom yy">
        <template v-if="detail_product.harga_modal!=0 && detail_product.harga_barang!=0">
            <div class="col-4 text-center">
                <small>Jumlah</small>
                <div class="input-group">
                    <div class="input-group-prepend">
                        <button class="btn btn-sm btn-light border" @click="jumlah_beli--" v-bind:disabled="jumlah_beli<=1">-</button>
                    </div>
                    <input required @input="cekJumlah()" type="number" min="1" v-model.number="jumlah_beli" class="form-control form-control-sm text-center">
                    <div class="input-group-append">
                        <button class="btn btn-sm btn-light border" @click="jumlah_beli++" v-bind:disabled="jumlah_beli_gabungan >= detail_product.stock">+</button>
                    </div>
                </div>
            </div>
            <div class="col-8 px-1">
                <!-- <small>Tanya di WA <a target="_blank" href="https://wa.me/6285389639356?text=Hayy...TukarJual.com" class="text-info">085389639356</a></small> -->
                <small>Tanya di WA <a target="_blank" href="tukarjual://webview?url=https://api.whatsapp.com/send?phone=6285389639356" class="text-info">085389639356</a></small>
                <button :disabled="$store.state.cart.cancelToken!=null||jumlah_beli_gabungan>detail_product.stock||jumlah_beli==0||detail_product.stock==0" class="btn btn-sm tombol btn-block text-white" @click="addProductToCart">
                    <i v-if="$store.state.cart.cart_product==null||$store.state.cart.cancelToken!=null" class="fa fa-spinner fa-spin"></i>
                    <template v-else>Beli Sekarang</template>
                </button>
                <small>Ada nang labih murah dari kami <a href="gasan-pian.html" class="badge badge-info">laporkan</a></small>
            </div>
        </template>
        <template v-else>
            <!-- <div class="col-12">
                <a target="_blank" :href="'https://wa.me/6285389639356?text=Saya Mau Pesan' + detail_product.nama_barang" :disabled="barang==null||tombol==false||jumlah_beli==0" class="btn btn-success btn-sm btn-block">
                    <i v-if="barang==null||tombol==false" class="fa fa-spinner fa-spin"></i>
                    <template v-else>WhatsApp</template>
                </a>
            </div> -->
        </template>
    </div>
</template>
<script>
export default {
    name:"FormBeli",
    props:{
        detail_product:Object
    },
    data(){
        return {
            jumlah_beli:1,
        }
    },
    computed:{
        jumlah_beli_gabungan(){
            return this.$store.getters.jumlah_beli_gabungan(this.detail_product.id_barang, this.jumlah_beli);
        }
    },
    watch:{
        jumlah_beli(){
            this.$emit('setJumlahBeli',this.jumlah_beli);
        }
    },
    methods:{
        cekJumlah() {
			if (this.jumlah_beli_gabungan > this.detail_product.stock) {
				this.jumlah_beli = parseInt(this.detail_product.stock)-this.$store.getters.jumlah_beli_gabungan(this.detail_product.id_barang, 0);
			}
        },
        addProductToCart(){
            if(this.$store.state.login.ifLogin){
                this.$store.dispatch('addProductToCart',{
                    product_id:this.detail_product.id_barang, 
                    quantity:this.jumlah_beli_gabungan
                });
            }else{
                this.$router.push({name:'login'});
            }
        }
    }
}
</script>
<style>
    .yy {
        max-width :460px;
        margin: 0 auto;
        background-color: #FFFFFF;
    }
</style>