<template>
    <div>
        <p class="font-weight-bold mb-0" v-if="product!=null">Rekomendasi Gasan Pian</p>
        <div class="row pb-5 mb-5">
            <template v-if="product!=null">
                <div class="col-4 p-1" v-for="product in product" :key="product.id_barang">
                    <router-link :to="{name:'Product',params:{kategori:product.kategori, kategori_id:product.id_kategory_barang,product_id:product.id_barang,product:product.seo_nama_barang}}">
                        <div class="card product">
                            <!-- <img v-if="product.depan!=null&&product.depan!=''" class="lazy img-fluid card-img-top" :src="$store.state.url_images+'loading_post_produk.svg'" :data-src="$store.state.url_product+product.depan" :alt="product.nama_barang"> -->
                            <img v-if="product.depan!=null&&product.depan!=''" class="lazy img-fluid card-img-top" :src="$store.state.url_product+product.depan" :alt="product.nama_barang">
                            <img v-else class="img-fluid card-img-top" :src="$store.state.url_images+'default_image.jpeg'" :alt="product.nama_barang">
                            <div class="card-body">
                                <p class="product-name small">{{product.nama_barang}}</p>
                                <strong class="small font-weight-bold text-success">
                                    <template>
                                    Rp {{toCurrency(product.harga_barang)}}
                                    </template>
                                </strong>
                                <br>
                                <router-link :to="{name:'Store',params:{store:product.penjual_model.seo_nama_toko}}" class="small text-muted sell-name">{{product.penjual_model.nama_toko}}</router-link>
                            </div>
                        </div>
                    </router-link>
                </div>
            </template>
        </div>
    </div>
</template>
<script>
export default {
    name:'Suggested',
    data(){
        return {
            product:null
        }
    },
    created(){
        this.axios.get(this.$store.state.url_api+ 'product/suggested').then(response => {
			this.product=response.data;
        })
    },
    methods:{
        toCurrency(angka) {
			angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return x1 + x2;
		},
    }
}
</script>