<template>
    <div class="p-1">
		<div v-if="detail_product==null" class="row pt-5 mt-3">
			<img :src="$store.state.url_images+'loading.svg'" class="img-fluid d-flex mx-auto" style="height: 100px">
		</div>
        <div v-if="detail_product!=null" id="carouselExampleControls" class="carousel slide pt-5 mt-3" data-ride="carousel">
            <div class="carousel-inner">
                <div v-if="detail_product.depan!=null&&detail_product.depan!=''" class="carousel-item active">
                    <img class="d-block w-100" :src="$store.state.url_product+detail_product.depan" :alt="detail_product.nama_barang">
                    <div class="carousel-caption d-none d-md-block"><small>Gambar Depan Produk</small></div>
                </div>
                <div v-if="detail_product.kanan!=null&&detail_product.kanan!=''" :class="'carousel-item' + (detail_product.depan==null&& detail_product.depan=='' ? 'active' : '')">
                    <img class="d-block w-100" :src="$store.state.url_product+detail_product.kanan" :alt="detail_product.nama_barang">
                    <div class="carousel-caption d-none d-md-block"><small>Gambar Belakang Produk</small></div>
                </div>
                <div v-if="detail_product.kiri!=null&&detail_product.kiri!=''" :class="'carousel-item' + (detail_product.depan==null && detail_product.kanan==null && detail_product.depan=='' && detail_product.kanan=='' ? 'active' : '')">
                    <img class="d-block w-100" :src="$store.state.url_product+detail_product.kiri" :alt="detail_product.nama_barang">
                    <div class="carousel-caption d-none d-md-block"><small>Gambar Berat Produk</small></div>
                </div>
                <div v-if="detail_product.belakang!=null&&detail_product.belakang!=''" :class="'carousel-item' + (detail_product.depan==null && detail_product.kanan==null && detail_product.kiri==null && detail_product.depan=='' && detail_product.kanan=='' && detail_product.kiri=='' ? 'active' : '')">
                <img class="d-block w-100" :src="$store.state.url_product+detail_product.belakang" :alt="detail_product.nama_barang">
                    <div class="carousel-caption d-none d-md-block"><small>Gambar Tanggal Kedaluwarsa Produk</small></div>
                </div>
                <div v-if="(detail_product.depan==null&&detail_product.kanan==null&&detail_product.kiri==null&&detail_product.belakang==null)||(detail_product.depan==''&&detail_product.kanan==''&&detail_product.kiri==''&&detail_product.belakang=='')" class="carousel-item active">
                    <img class="d-block w-100" :src="$store.state.url_images+'default_image.jpeg'" :alt="detail_product.nama_barang">
                    <div class="carousel-caption d-none d-md-block"><small>Gambar Product</small></div>
                </div>
            </div>
            <template v-if="detail_product.depan!=null&&detail_product.kanan!=null&&detail_product.kiri!=null&&detail_product.belakang!=null&&detail_product.depan!=''&&detail_product.kanan!=''&&detail_product.kiri!=''&&detail_product.belakang!=''">
                <a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="sr-only">Previous</span>
                </a>
                <a class="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="sr-only">Next</span>
                </a>
            </template>
        </div>
        <template v-if="detail_product!=null">
            <h5 class="text-capitalize mt-3">{{detail_product.nama_barang}}</h5>
            <h5 v-if="detail_product.harga_modal!=0 && detail_product.harga_barang!=0">
                <span class="text-success font-weight-bold mt-5">
                    Rp {{toCurrency(harga_barang!=0 ? harga_barang : detail_product.harga_barang)}} ,-
                </span>
            </h5>
        </template>
        <!-- <hr> -->
        <!-- <b>Keterangan Produk</b> -->
        <template v-if="detail_product!=null">
            <div v-html="detail_product.deskripsi_barang" class="mb-5"></div>
        </template>
        
        <!-- <hr> -->
        <!-- tawarkan barang letaknya disini -->
        <Suggested />

        <FormBeli @setJumlahBeli="setJumlahBeli" v-if="detail_product!=null" :detail_product="detail_product" />  
    </div>
</template>
<script>
import FormBeli from '@/components/Product/FormBeli'
import Suggested from '@/components/Product/Suggested'
export default {
    name:"Product",
    components:{
        FormBeli,
        Suggested
    },
    data(){
        return {
            detail_product:null,
            jumlah_beli:1
        }
    },
    computed:{
        harga_barang(){
            return this.jumlah_beli*this.detail_product.harga_barang;
        }
    },
    created(){
        this.get_detail_product();
    },
    methods:{
        setJumlahBeli(value){
            this.jumlah_beli = value;
        },
        toCurrency(angka) {
			angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return x1 + x2;
		},
        get_detail_product(){
			this.axios.get(this.$store.state.url_api+ 'product/details/' + this.$route.params.product_id).then(response => {
				this.detail_product=response.data;
			})
		},
    }
}
</script>      